import { Component, OnInit } from '@angular/core';
import { Http, HttpModule } from '@angular/http';
import { HomesService } from './../services/homes.service';
import { UtilService} from './../services/util.service';

@Component({
  selector: 'app-home-listing',
  templateUrl: './home-listing.component.html',
  styleUrls: ['./home-listing.component.css']
})
export class HomeListingComponent implements OnInit {

  homes: Array<any>;
  error: string;
  sortField: string = 'price';
  sortDirection: string = 'asc';
  sortFields: Array<string> = [
    'address',
    'area',
    'bathrooms',
    'bedrooms',
    'price',
    'type'
  ];

  constructor (
    private http: Http, 
    private homesService: HomesService,
    private utilService: UtilService
  ) { }

  ngOnInit() {
    //make an http request
    this.homesService.getAllHomes()
    .subscribe(
      data => this.homes = data,
      error => this.error = error.statusText
    );

    this.homesService.newHomeSubject.subscribe(
      data => this.homes = [data, ...this.homes]
    );
  }
}



